.addRoomAvatarModal {
  background-color: white;
  color: black;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--empty-space-color--);
  color: lavender;
  padding: 20px 40px;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;

  -webkit-box-shadow: 0px 0px 15px -5px rgba(151, 201, 217, 1);
  -moz-box-shadow: 0px 0px 15px -5px rgba(151, 201, 217, 1);
  box-shadow: 0px 0px 15px -5px rgba(151, 201, 217, 1);
}

