.notFound {
  height: 100%;
  position: relative;
}

.notFound__wrapper {
  height: 40%;
  width: 500px;

  transition: all 0.4s ease;
  text-align: center;
  background-color: var(--content-space-color--);
  border-radius: 10px;
  box-shadow: 0 1px 20px rgba(0, 0, 0, 0.3);

  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);

  
  -webkit-box-shadow: -1px -1px 21px -7px rgba(151,201,217,1);
-moz-box-shadow: -1px -1px 21px -7px rgba(151,201,217,1);
box-shadow: -1px -1px 21px -7px rgba(151,201,217,1);
}

@media screen and (max-width: 600px){
  .notFound__wrapper{
    height: 30%;
    width: 70%;
  }
}